<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.user_id" placeholder="user_id"></el-input>
				</el-form-item>
				<el-form-item>
					<el-input v-model="filters.user_name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getusers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>
		
		<!---->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column type="index" width="60">
			</el-table-column>
			<el-table-column prop="user_id" label="user_id" width="120" sortable>
			</el-table-column>
			<el-table-column prop="user_name" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="uploaddate" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="uploadtimes" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="user_certificate" label="" width="120" :formatter="formatCert" sortable>
			</el-table-column>
			<el-table-column prop="user_auth" label="" width="120" :formatter="formatAuth" sortable>
			</el-table-column>

			<el-table-column prop="data_download_auth" label="" width="120" :formatter="formatDownload" sortable>
			</el-table-column>
			<el-table-column prop="data_browse_auth" label="" width="120" :formatter="formatbrowse" sortable>
			</el-table-column>
			<el-table-column prop="device_manage_auth" label="" width="120" :formatter="formatDevice" sortable>
			</el-table-column>
			<el-table-column prop="user_manage_auth" label="" width="120" :formatter="formatUser" sortable>
			</el-table-column>

			<el-table-column label="" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<!-- <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button> -->
				</template>
			</el-table-column>
		</el-table>
		
		<!---->
		<el-col :span="24" class="toolbar">
			<!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> -->
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>
		
		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="user_id" prop="user_id">
					<el-input v-model="editForm.user_id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="user_name">
					<el-input v-model="editForm.user_name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.user_certificate">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.user_auth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>

				<el-form-item label="">
					<el-radio-group v-model="editForm.data_download_auth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.data_browse_auth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.device_manage_auth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="editForm.user_manage_auth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>
		
		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="user_name">
					<el-input v-model="addForm.user_name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="user_id" prop="user_id">
					<el-input v-model="addForm.user_id" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-date-picker type="date" placeholder="" v-model="addForm.uploaddate"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-input-number v-model="addForm.uploadtimes" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.user_certificate">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.user_auth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>

				<el-form-item label="">
					<el-radio-group v-model="addForm.data_download_auth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.data_browse_auth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.device_manage_auth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addForm.user_manage_auth">
						<el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="0"></el-radio>
					</el-radio-group>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
		
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getUserListPage, editUser, addDevice } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					user_name: '',
					user_id: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [], //
				
				editFormVisible: false, //
				editLoading: false,
				editFormRules: {
					user_name: [
						{ required: true, message: '', trigger: 'blur' }
					],
					user_id: [
						{ required: true, message: 'user_id', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id: 0,
					user_id: '',
					user_name: '',
					user_certificate:-1,
					user_auth:-1,
					data_download_auth: -1,
					data_browse_auth: -1,
					device_manage_auth:-1,
					user_manage_auth:-1
				},
				
				addFormVisible: false, //
				addLoading: false,
				addFormRules: {
					user_name: [
						{ required: true, message: '', trigger: 'blur' }
					],
					user_id: [
						{ required: true, message: 'user_id', trigger: 'blur' }
					]
				},
				//
				addForm: {
					user_name: '',
					user_id: '',
					uploaddate: '',
					uploadtimes: 0,
					user_certificate:-1,
					user_auth:-1,
					data_download_auth: -1,
					data_browse_auth: -1,
					device_manage_auth:-1,
					user_manage_auth:-1
				},
				
			}
		},
		
		methods: {
			//
			formatCert: function (row, column) {
				return row.user_certificate == 1 ? '' : row.user_certificate == 0 ? '' : '';
			},
			//
			formatAuth: function (row, colomn) {
				return row.user_auth == 1 ? '' : row.user_auth == 0 ? '' : '';
			},
			
			//
			formatDownload: function (row, column) {
				return row.data_download_auth == 1 ? '' : row.data_download_auth == 0 ? '' : '';
			},
			//
			formatbrowse: function (row, colomn) {
				return row.data_browse_auth == 1 ? '' : row.data_browse_auth == 0 ? '' : '';
			},
			//
			formatDevice: function (row, column) {
				return row.device_manage_auth == 1 ? '' : row.device_manage_auth == 0 ? '' : '';
			},
			//
			formatUser: function (row, colomn) {
				return row.user_manage_auth == 1 ? '' : row.user_manage_auth == 0 ? '' : '';
			},

			handleCurrentChange(val) {
				this.page = val;
				this.getusers();
			},
			//
			getusers() {
				let para = {
					page: this.page,
					user_name: this.filters.user_name,
					user_id: this.filters.user_id
				};
				console.log(para)
				this.listLoading = true;
				//NProgress.start();
				getUserListPage(para).then((res) => {
					console.log(res)
					this.total = res.data.total;
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {
			
				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					user_name: '',
					user_id: '',
					uploaddate: '',
					uploadtimes: 0,
					user_certificate:-1,
					user_auth:-1,
					data_download_auth: -1,
					data_browse_auth: -1,
					device_manage_auth:-1,
					user_manage_auth:-1
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							console.log(para)
							// para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getusers();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.uploaddate = (!para.uploaddate || para.uploaddate == '') ? '' : util.formatDate.format(new Date(para.uploaddate), 'yyyy-MM-dd');
							addDevice(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getusers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {
			
				});
			}
		},
		
		mounted() {
			this.getusers();
		}
	}

</script>

<style scoped>

</style>


